.login {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login_link_button {
  width: 100%;
  height: 100%;
  background: none;
  border: none;
  font-size: 51px;
  color: rgb(0, 162, 255);
}

.login_link_button:hover {
  cursor: pointer;
  color: rgb(0, 140, 255);
  text-decoration: underline;
}

.login_link {
  width: 45%;
  height: 8%;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
}
